<template>
    <div class="navBar van-nav-bar" :style="navBarStyle">
        <div class="left" v-if="isShowLeft"  @click="$emit('onLeftClick')" :style="bothWidth">
            <!-- 默认状态 -->
            <img v-if="isShowBack" :src="backImg" alt="" srcset="" >
            <!-- 定制左侧具名插槽 -->
            <slot name="nav-left"></slot>
        </div>
        <div class="center">
            <!-- 默认状态 -->
            <span class="title" v-if="isDefault">{{pageName}}</span>
            <!-- 定制中间具名插槽 -->
            <slot name="nav-center"></slot>
        </div>
        <div class="right" v-if="isShowRight" :style="bothWidth">
            <!-- 定制右侧具名插槽 -->
            <slot name="nav-right"></slot>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        // 是否展示默认状态
        isDefault: {
            default: true,
            type: Boolean
        },
        // 两边的宽度
        bothWidth:{
            type: Object,
            default: function () {
                return {
                    width:'36px'
                }
            }
        },
        // 右侧是否展示
        isShowRight:{
            default: true,
            type: Boolean
        },
         // 左侧是否展示
        isShowLeft:{
            default: true,
            type: Boolean
        },
        // 是否展示默认后退按钮
        isShowBack: {
            default: true,
            type: Boolean
        },
        // 展示后退图片样式
        leftImgType:{
            type:String,
            default:'0',
        },
        // 默认状态下页面标题的名称
        pageName: {
            default: '',
            type: String
        },
        // navBar样式
        navBarStyle: {
            type: Object,
            default: function () {
                return {
                    backgroundColor: '#fff'
                }
            }
        }
    },
    data(){
        return{
            backImg:require('@/assets/imgs/title/nav_return_white.png')
        }
    },

    mounted(){
        this.isLeftImg();
    },
    methods:{
        // 判断使用不同的左侧图片
        isLeftImg(){
            switch (this.leftImgType){
                case '0':
                    this.backImg = require('@/assets/imgs/title/nav_return_white.png');
                    break;
                case '1':
                    this.backImg = require('@/assets/imgs/title/nav_return_white.png');
                    break;
            }
        }
    }
}
</script>

